import { useState } from "react";
import { Calendar, Filter, X } from "lucide-react";

const OrdersFilters = () => {
  const [isFiltersOpen, setIsFiltersOpen] = useState(false);

  // Toggle mobile filters panel
  const toggleFilters = () => {
    setIsFiltersOpen(!isFiltersOpen);
  };

  return (
    <div className="mb-6">
      {/* Mobile filter toggle button */}
      <div className="md:hidden mb-4">
        <button
          onClick={toggleFilters}
          className="flex items-center gap-2 text-zinc-700 dark:text-zinc-300 bg-white dark:bg-zinc-800 px-4 py-2 rounded-xl shadow-sm border border-zinc-200 dark:border-zinc-700"
        >
          <Filter size={18} />
          <span className="font-medium">فیلترها</span>
        </button>
      </div>

      {/* Desktop filters (always visible) & Mobile filters (conditional) */}
      <div
        className={`${
          isFiltersOpen ? "block" : "hidden"
        } md:block bg-white dark:bg-zinc-800 rounded-xl shadow-md p-4 mb-4 transition-all`}
      >
        {/* Mobile header with close button */}
        <div className="flex items-center justify-between md:hidden mb-3">
          <h3 className="font-medium text-zinc-800 dark:text-zinc-200">
            فیلتر سفارش‌ها
          </h3>
          <button
            onClick={toggleFilters}
            className="text-zinc-500 hover:text-zinc-700 dark:text-zinc-400 dark:hover:text-zinc-200"
          >
            <X size={20} />
          </button>
        </div>

        {/* Filters container */}
        <div className="flex flex-col md:flex-row md:flex-wrap md:items-end lg:items-end lg:justify-between gap-4">
          {/* Date From Filter */}
          <div className="w-full sm:w-1/2 md:w-auto lg:w-auto">
            <label className="block text-sm font-medium mb-1 text-zinc-600 dark:text-zinc-400">
              از تاریخ:
            </label>
            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <Calendar
                  size={16}
                  className="text-zinc-400 dark:text-zinc-500"
                />
              </div>
              <input
                type="date"
                className="w-full pr-3 pl-10 py-2 border border-zinc-300 dark:border-zinc-600 rounded-lg text-zinc-700 dark:text-zinc-300 bg-white dark:bg-zinc-900 focus:ring-2 focus:ring-zinc-400 dark:focus:ring-zinc-500 focus:border-zinc-400 dark:focus:border-zinc-500"
              />
            </div>
          </div>

          {/* Date To Filter */}
          <div className="w-full sm:w-1/2 md:w-auto lg:w-auto">
            <label className="block text-sm font-medium mb-1 text-zinc-600 dark:text-zinc-400">
              تا تاریخ:
            </label>
            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <Calendar
                  size={16}
                  className="text-zinc-400 dark:text-zinc-500"
                />
              </div>
              <input
                type="date"
                className="w-full pr-3 pl-10 py-2 border border-zinc-300 dark:border-zinc-600 rounded-lg text-zinc-700 dark:text-zinc-300 bg-white dark:bg-zinc-900 focus:ring-2 focus:ring-zinc-400 dark:focus:ring-zinc-500 focus:border-zinc-400 dark:focus:border-zinc-500"
              />
            </div>
          </div>

          {/* Payment Status Filter */}
          <div className="w-full sm:w-1/2 md:w-auto lg:w-auto">
            <label className="block text-sm font-medium mb-1 text-zinc-600 dark:text-zinc-400">
              وضعیت پرداخت:
            </label>
            <select className="w-full pr-3 pl-3 py-2 border border-zinc-300 dark:border-zinc-600 rounded-lg text-zinc-700 dark:text-zinc-300 bg-white dark:bg-zinc-900 focus:ring-2 focus:ring-zinc-400 dark:focus:ring-zinc-500 focus:border-zinc-400 dark:focus:border-zinc-500">
              <option value="">همه</option>
              <option value="paid">پرداخت شده</option>
              <option value="unpaid">در انتظار پرداخت</option>
            </select>
          </div>

          {/* Fulfillment Status Filter */}
          <div className="w-full sm:w-1/2 md:w-auto lg:w-auto">
            <label className="block text-sm font-medium mb-1 text-zinc-600 dark:text-zinc-400">
              وضعیت ارسال:
            </label>
            <select className="w-full pr-3 pl-3 py-2 border border-zinc-300 dark:border-zinc-600 rounded-lg text-zinc-700 dark:text-zinc-300 bg-white dark:bg-zinc-900 focus:ring-2 focus:ring-zinc-400 dark:focus:ring-zinc-500 focus:border-zinc-400 dark:focus:border-zinc-500">
              <option value="">همه</option>
              <option value="shipped">ارسال شده</option>
              <option value="processing">در حال پردازش</option>
            </select>
          </div>

          {/* Apply Filters Button */}
          <div className="w-full md:w-auto md:mt-0">
            <button className="w-full md:w-auto px-4 py-2 bg-zinc-800 hover:bg-zinc-900 dark:bg-zinc-700 dark:hover:bg-zinc-600 text-white rounded-lg text-sm font-medium transition-colors">
              اعمال فیلترها
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OrdersFilters;
