import { Eye, Trash2, MoreHorizontal, ChevronDown } from "lucide-react";
import { Helmet } from "react-helmet";
import { useState } from "react";

const OrdersTable = ({ orders, onView, onDelete }) => {
  const [expandedOrder, setExpandedOrder] = useState(null);

  const statusColor = {
    paid: "text-green-600 bg-green-50 dark:bg-green-900/20",
    unpaid: "text-red-500 bg-red-50 dark:bg-red-900/20",
  };

  const fulfillmentBadge = {
    shipped:
      "bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-400",
    processing:
      "bg-yellow-100 text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-400",
  };

  const toggleOrderDetails = (orderId) => {
    setExpandedOrder(expandedOrder === orderId ? null : orderId);
  };

  // Format date for better display
  const formatDate = (dateString) => {
    try {
      const date = new Date(dateString);
      return date.toLocaleDateString("fa-IR");
    } catch (e) {
      return dateString;
    }
  };

  return (
    <>
      <Helmet>
        <title>المکس | سفارش ها</title>
      </Helmet>

      {/* Desktop table view */}
      <div className="hidden md:block overflow-x-auto bg-white dark:bg-neutral-800 rounded-xl shadow-md">
        <table className="min-w-full text-sm text-right">
          <thead className="bg-gray-100 dark:bg-neutral-700 text-gray-600 dark:text-gray-300">
            <tr>
              <th className="p-3 font-medium">شناسه</th>
              <th className="p-3 font-medium">مشتری</th>
              <th className="p-3 font-medium">پرداخت</th>
              <th className="p-3 font-medium">ارسال</th>
              <th className="p-3 font-medium">تاریخ</th>
              <th className="p-3 font-medium">مبلغ</th>
              <th className="p-3 font-medium">عملیات</th>
            </tr>
          </thead>
          <tbody className="divide-y divide-gray-200 dark:divide-neutral-700">
            {orders.map((order) => (
              <tr
                key={order?.id}
                className="hover:bg-gray-50 dark:hover:bg-neutral-700 text-gray-600 dark:text-gray-300 transition-colors"
              >
                <td className="p-4 font-mono text-sm">{order?.id}</td>
                <td className="p-4">
                  {order?.customer_name ? order.customer_name : "اسم مشتری"}
                </td>
                <td className="p-4">
                  <span
                    className={`px-2 py-1 rounded-full text-xs font-medium ${
                      statusColor[order?.status]
                    }`}
                  >
                    {order?.status === "paid" ? "پرداخت شده" : "در انتظار"}
                  </span>
                </td>
                <td className="p-4">
                  <span
                    className={`px-2 py-1 rounded-full text-xs font-medium ${
                      fulfillmentBadge[order?.fulfillment]
                    }`}
                  >
                    {order?.fulfillment === "shipped"
                      ? "ارسال شده"
                      : "در حال پردازش"}
                  </span>
                </td>
                <td className="p-4">
                  {new Date(order?.order_date).toLocaleDateString("fa-IR")}
                </td>
                <td className="p-4 font-bold">
                  {Number(order?.total)?.toLocaleString("fa-IR")} تومان
                </td>
                <td className="p-4">
                  <div className="flex flex-row items-center gap-2 justify-start">
                    <button
                      onClick={() => onView(order)}
                      className="p-1.5 rounded-full text-blue-500 hover:text-blue-700 hover:bg-blue-50 dark:hover:bg-blue-900/20 transition-colors"
                      aria-label="مشاهده سفارش"
                      title="مشاهده سفارش"
                    >
                      <Eye size={18} />
                    </button>
                    <button
                      onClick={() => onDelete && onDelete(order)}
                      className="p-1.5 rounded-full text-red-500 hover:text-red-700 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors"
                      aria-label="حذف سفارش"
                      title="حذف سفارش"
                    >
                      <Trash2 size={18} />
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* Mobile card view */}
      <div className="md:hidden space-y-4">
        {orders.map((order) => (
          <div
            key={order.id}
            className="bg-white dark:bg-neutral-800 rounded-xl shadow-md overflow-hidden"
          >
            <div
              className="p-4 flex justify-between items-center cursor-pointer"
              onClick={() => toggleOrderDetails(order.id)}
            >
              <div className="flex flex-col">
                <div className="flex items-center gap-2">
                  <span className="font-bold text-gray-800 dark:text-gray-200">
                    {order?.name ? order.name : "اسم مشتری"}
                  </span>
                  <span
                    className={`px-2 py-0.5 rounded-full text-xs font-medium ${
                      statusColor[order?.status]
                    }`}
                  >
                    {order?.status === "paid" ? "پرداخت شده" : "در انتظار"}
                  </span>
                </div>
                <div className="text-sm text-gray-500 dark:text-gray-400 mt-1">
                  {formatDate(order?.created_at)} •{" "}
                  {Number(order?.total)?.toLocaleString("fa-IR")} تومان
                </div>
              </div>
              <ChevronDown
                size={20}
                className={`text-gray-500 transition-transform ${
                  expandedOrder === order?.id ? "rotate-180" : ""
                }`}
              />
            </div>

            {expandedOrder === order?.id && (
              <div className="px-4 pb-4 pt-1 border-t border-gray-100 dark:border-neutral-700">
                <div className="space-y-2 text-sm">
                  <div className="flex justify-between">
                    <span className="text-gray-500 dark:text-gray-400">
                      شناسه:
                    </span>
                    <span className="font-mono text-gray-700 dark:text-gray-300">
                      {order?.id}
                    </span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-gray-500 dark:text-gray-400">
                      وضعیت ارسال:
                    </span>
                    <span
                      className={`px-2 py-0.5 rounded-full text-xs font-medium ${
                        fulfillmentBadge[order.fulfillment]
                      }`}
                    >
                      {order.fulfillment === "shipped"
                        ? "ارسال شده"
                        : "در حال پردازش"}
                    </span>
                  </div>
                  <div className="flex justify-between pt-2">
                    <button
                      onClick={(e) => {
                        e.stopPropagation();
                        onView(order);
                      }}
                      className="flex items-center gap-1 px-3 py-1.5 rounded-lg bg-blue-50 text-blue-600 hover:bg-blue-100 dark:bg-blue-900/20 dark:text-blue-400 dark:hover:bg-blue-900/30 text-xs font-medium"
                    >
                      <Eye size={14} />
                      مشاهده جزئیات
                    </button>
                    <button
                      onClick={(e) => {
                        e.stopPropagation();
                        onDelete && onDelete(order);
                      }}
                      className="flex items-center gap-1 px-3 py-1.5 rounded-lg bg-red-50 text-red-600 hover:bg-red-100 dark:bg-red-900/20 dark:text-red-400 dark:hover:bg-red-900/30 text-xs font-medium"
                    >
                      <Trash2 size={14} />
                      حذف
                    </button>
                  </div>
                </div>
              </div>
            )}
          </div>
        ))}
      </div>

      {/* Empty state */}
      {orders.length === 0 && (
        <div className="bg-white dark:bg-neutral-800 rounded-xl shadow-md p-8 text-center">
          <div className="text-gray-500 dark:text-gray-400">
            هیچ سفارشی یافت نشد
          </div>
        </div>
      )}
    </>
  );
};

export default OrdersTable;
