import { X, Truck, CreditCard } from "lucide-react";
import { useEffect } from "react";

const OrderDetailModal = ({
  isOpen,
  onClose,
  order,
  onUpdateFulfillment,
  onConfirmPayment,
}) => {
  if (!isOpen || !order) return null;

  return (
    <div className="fixed inset-0 z-50 bg-black bg-opacity-50 flex justify-center items-center px-4">
      <div className="bg-white dark:bg-neutral-800 w-full max-w-2xl rounded-xl shadow-xl p-6 relative text-right font-vazir">
        <button
          onClick={onClose}
          className="absolute top-4 left-4 text-gray-500 hover:text-red-500"
        >
          <X />
        </button>

        <h2 className="text-xl font-bold text-gray-800 dark:text-white mb-4">
          جزئیات سفارش
        </h2>

        <div className="space-y-3 text-sm text-gray-700 dark:text-gray-300">
          <p>
            <strong>شناسه:</strong> {order?.id}
          </p>
          <p>
            <strong>مشتری:</strong> {order?.name}
          </p>
          <p>
            <strong>محصولات:</strong>
          </p>
          <p>
            <strong>تاریخ سفارش:</strong> {order?.date}
          </p>
          <p>
            <strong>مبلغ کل:</strong>{" "}
            {Number(order?.total)?.toLocaleString("fa-IR")} تومان
          </p>
          <p className="flex items-center gap-2">
            <CreditCard size={18} /> وضعیت پرداخت:{" "}
            {order.status === "paid" ? "پرداخت شده" : "در انتظار پرداخت"}
          </p>
          <p className="flex items-center gap-2">
            <Truck size={18} /> وضعیت ارسال:{" "}
            {order.fulfillment === "shipped" ? "ارسال شده" : "در حال پردازش"}
          </p>
        </div>

        <div className="mt-6 flex gap-4">
          {order.status !== "paid" && (
            <button
              onClick={() => {
                onConfirmPayment(order.id);
                onClose();
              }}
              className="bg-green-600 text-white px-4 py-2 rounded-xl hover:bg-green-700 text-sm"
            >
              تایید پرداخت
            </button>
          )}
          <button
            onClick={() => {
              onUpdateFulfillment(order.id);
              onClose();
            }}
            className="bg-blue-600 text-white px-4 py-2 rounded-xl hover:bg-blue-700 text-sm"
          >
            تغییر وضعیت ارسال
          </button>
        </div>
      </div>
    </div>
  );
};

export default OrderDetailModal;
