import { useEffect, useState } from "react";
import OrdersFilters from "./OrdersFilters";
import OrdersTable from "./OrdersTable";
// import OrderDetailModal from "./OrderDetailModal";
import OrderDetailModal from "./OrderDetailsModal";
import * as XLSX from "xlsx";
import toast, { Toaster } from "react-hot-toast";
import { useOutletContext } from "react-router-dom";

const OrdersPage = () => {
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [orders, setOrders] = useState([]);
  const [exelOrders, setORders] = useState(null);

  useEffect(() => {
    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/orders/"
        : "https://allmaxsport.com/api/orders/";
    async function loadOrders() {
      const response = await fetch(API_URL);
      const data = await response.json();

      setOrders(data);
    }
    loadOrders();
  }, []);

  const { role } = useOutletContext();
  if (role !== "admin" && role !== "manager") return null; // Safety check

  const handleFulfillmentUpdate = (id) => {
    setOrders((prev) =>
      prev.map((o) =>
        o.id === id
          ? {
              ...o,
              fulfillment:
                o.fulfillment === "processing" ? "shipped" : "processing",
            }
          : o
      )
    );
    toast.success("وضعیت ارسال به‌روزرسانی شد");
  };

  const handlePaymentConfirm = (id) => {
    setOrders((prev) =>
      prev.map((o) => (o.id === id ? { ...o, status: "paid" } : o))
    );
    toast.success("پرداخت تایید شد");
  };

  const exportToExcel = () => {
    if (!orders || orders.length === 0) {
      toast.error("سفارشی برای خروجی وجود ندارد");
      return;
    }

    const localizedData = orders.map((order) => ({
      "شناسه سفارش": order.id,
      "نام مشتری": order.customer_name || "—",
      "وضعیت پرداخت":
        order.status === "paid" ? "پرداخت‌شده" : "در انتظار پرداخت",
      "وضعیت ارسال":
        order.fulfillment === "shipped" ? "ارسال‌شده" : "در حال پردازش",
      "مبلغ کل": order.total_price,
      "تاریخ سفارش": new Date(order.created_at).toLocaleDateString("fa-IR"),
    }));

    const worksheet = XLSX.utils.json_to_sheet(localizedData);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "سفارش‌ها");

    XLSX.writeFile(workbook, "گزارش_سفارشات.xlsx");
  };

  return (
    <div className="p-6 text-right font-vazir">
      <Toaster position="top-center" />
      <div className="flex flex-col md:flex-row justify-between items-center mb-6">
        <h2 className="text-2xl font-bold text-gray-800 dark:text-white mb-2 md:mb-0">
          مدیریت سفارش‌ها
        </h2>
        <button
          onClick={exportToExcel}
          className="bg-emerald-600 hover:bg-emerald-700 text-white px-4 py-2 rounded-xl"
        >
          دانلود اکسل سفارشات
        </button>
      </div>

      <OrdersFilters />
      <OrdersTable orders={orders} onView={(o) => setSelectedOrder(o)} />
      <OrderDetailModal
        isOpen={!!selectedOrder}
        order={selectedOrder}
        onClose={() => setSelectedOrder(null)}
        onUpdateFulfillment={handleFulfillmentUpdate}
        onConfirmPayment={handlePaymentConfirm}
      />
    </div>
  );
};

export default OrdersPage;
